import React from 'react';
import styled from 'styled-components';

const ErrorPage = () => {
  return (
    <Err>
        <header className='errHeader'>
            <div className="errbox">
              <h1 className='text-center' style={{fontSize:"50px"}}>404</h1>
                <h1>ERROR! Page not found!</h1>
            </div>
        </header>
    </Err>
  )
}
const Err = styled.div`
  .errHeader {
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  .errbox{
    margin: auto;
    h1{
      font-weight: bold;
    }
  }
`
export default ErrorPage